.body-container {
  color: #2d2e30;
  width: 1280px;
  background: #fff;
  margin: 0 auto;
  font-family: Helvetica Neue, Roboto, Verdana, Tahoma, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
}

.nav-container {
  height: 216px;
  background-image: url("../header-background.28cae044.jpg");
  justify-content: center;
  align-items: center;
  display: flex;
}

.main-container {
  background: #fff;
  margin-top: 20px;
}

.input-class {
  font-size: 40px;
}

.cards-list {
  flex-wrap: wrap;
  gap: 32px 16px;
  margin: 60px 30px;
  display: flex;
}

.card {
  width: 395px;
  height: 634px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .25);
}

.card-img {
  width: 395px;
  height: 574px;
  background: url("../film500.80a0ab88.jpg");
  border-radius: 5px;
}

.card-text {
  font-family: var(--font-family);
  text-transform: uppercase;
  color: #000;
  font-size: 20px;
  font-weight: 500;
  line-height: 1.2;
}

@media screen and (min-width: 768px) and (max-width: 1157px) {
  .body-container {
    width: 768px;
  }

  .cards-list {
    flex-wrap: wrap;
    gap: 32px;
    margin: 60px 32px;
    display: flex;
  }

  .card {
    width: 336px;
    height: 494px;
  }

  .card-img {
    width: 336px;
    height: 455px;
  }

  .card-text {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 1.33333;
  }
}

@media screen and (max-width: 767px) {
  .body-container {
    width: 320px;
  }

  .cards-list {
    flex-wrap: wrap;
    gap: 32px;
    margin: 20px 20px 40px;
    display: flex;
  }

  .card {
    width: 280px;
    height: 443px;
  }

  .card-img {
    width: 280px;
    height: 402px;
  }

  .card-text {
    font-family: var(--font-family);
    font-size: 12px;
    font-weight: 500;
    line-height: 1.33333;
  }
}

.pagination-new {
  justify-content: center;
  align-items: center;
  gap: 3px;
  margin: 40px auto;
  display: flex;
}

.pagination-new-list {
  flex-wrap: nowrap;
  gap: 3px;
  display: flex;
}

.pagination-new-button-first, .pagination-new-button-last {
  width: 40px;
  height: 40px;
  cursor: pointer;
  border: none;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.pagination-new-button-first:hover, .pagination-new-button-last:hover {
  background: rgba(247, 247, 247, .8);
}

.pagination-new-button-first {
  transform: rotate(-180deg);
}

.pagination-new-button-first:disabled, .pagination-new-button-last:disabled {
  cursor: not-allowed;
  background: #979292;
}

.pagination-new-button-first:disabled:hover, .pagination-new-button-last:disabled:hover {
  background: #979292;
}

.pagination-new-dots, .pagination-new-numb {
  color: #000;
  width: 40px;
  height: 40px;
  cursor: pointer;
  -ms-user-select: none;
  user-select: none;
  justify-content: center;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.33333;
  display: flex;
}

.pagination-new-numb {
  border-radius: 5px;
}

.pagination-new-dots {
  display: none;
}

.pagination-new li.active, .pagination-new-numb:hover {
  color: #fff;
  background-color: rgba(255, 107, 8, .5);
}

@media screen and (min-width: 768px) {
  .pagination-new {
    margin: 60px auto;
  }

  .pagination-new-dots {
    cursor: default;
    -ms-user-select: none;
    user-select: none;
    width: auto;
    padding-bottom: 8px;
    display: block;
  }
}

.footer-container {
  background: #e0cece;
}

